<template><section><html><head></head><body><h1>RectangleGraphics</h1>
<p>The <code v-pre="">vc-graphics-rectangle</code> component is used to load a Rectangle that conforms to the curvature of the globe and can be placed on the surface or at altitude and can optionally be extruded into a volume. Need to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load RectangleGraphics with <code v-pre="">vc-graphics-rectangle</code></h3>
<h4>Preview</h4>
<doc-preview>
    <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :description="description" :rectangle.sync="rectangle1">
          <vc-graphics-rectangle ref="rectangle1" :coordinates="coordinates1" :material="material1"></vc-graphics-rectangle>
        </vc-entity>
        <vc-entity :description="description" :rectangle.sync="rectangle2">
          <vc-graphics-rectangle :coordinates="coordinates2" :material="material2" :rotation="rotation2" :extruded-height="300000.0" :height="100000.0" :outline="true" :outline-color="outlineColor2" ref="rectangle2"></vc-graphics-rectangle>
        </vc-entity>
        <vc-entity :description="description" :rectangle.sync="rectangle3">
          <vc-graphics-rectangle :coordinates="coordinates3" :material="material3" :rotation="rotation3" :st-rotation="stRotation3" :classification-type="classificationType3" ref="rectangle3"></vc-graphics-rectangle>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
